.header-container {
  height: 70px;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

@media screen and (max-width: 768px) {
  .header-container {
    align-items: center;
    display: flex;
  }
}

@media screen and (min-width: 1158px) {
  .header-container {
    align-items: center;
  }
}

@media screen and (max-width: 767px) {
  .header-nav {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }
}

.header-burger {
  width: 32px;
  height: 32px;
  color: #050505;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
  padding: 0;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header-nav {
    flex-grow: 1;
    align-items: center;
    gap: 76px;
    display: flex;
  }

  .header-burger {
    display: none;
  }
}

.navig-logo {
  color: var(--primaty-black-color);
  margin-left: 20px;
  padding-right: 217px;
  font-family: Inter, sans-serif;
  font-size: 1.1rem;
  font-weight: 600;
  text-decoration: none;
  display: block;
}

.navig-logoitem {
  color: var(--accent-color);
  text-decoration: none;
}

.header-menu {
  flex-grow: 1;
  gap: 16px;
  display: flex;
}

@media screen and (min-width: 1158px) {
  .header-menu {
    gap: 16px;
  }
}

.header-menu, .header-contact {
  display: none;
}

.header-menu-contect {
  width: 24px;
  height: 24px;
  fill: none;
  stroke: #000;
  stroke-width: 2px;
  flex-direction: row;
  gap: 40px;
}

.header-menu-switch {
  display: block;
}

@media screen and (min-width: 768px) {
  .header-menu-switch, .header-menu-contect {
    width: 24px;
    height: 24px;
    fill: none;
    stroke: #000;
    stroke-width: 2px;
    width: 100%;
    flex-direction: column;
    gap: 18px;
    display: flex;
  }

  .header-menu-contect a {
    padding: 0;
  }

  .header-contact, .header-menu {
    gap: 20px;
    display: flex;
  }
}

@media screen and (min-width: 1200px) {
  .header-menu-switch, .header-menu-contect {
    width: 100%;
    flex-direction: row;
    gap: 40px;
  }
}

.link {
  color: var(--color-two);
  padding: 24px 0;
  font-family: Inter, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 14.52px;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: relative;
}

.link:hover, .link:focus {
  color: var(--accent-color);
}

.link.current:after {
  width: 100%;
  height: 16px;
  display: block;
  position: absolute;
}

.current:hover, .current:focus {
  color: var(--accent-color);
}

.mobile-menu {
  width: 100%;
  background: var(--accent-color);
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  transform: translateX(100%);
}

@media screen and (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

.mobile-menu.is-open {
  transform: translateX(0);
}

.model-close-js {
  width: 32px;
  height: 32px;
  cursor: pointer;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 28px;
  right: 28px;
}

.model-close-js:hover, .model-close-js:focus {
  background: var(--primary-white-color);
  fill: var(--color-inverted);
  border-radius: 2px;
}

.close {
  width: 100%;
  height: 100%;
  color: var(--primary-white-color);
  fill: currentColor;
  display: inline-block;
}

.header-nav-modal {
  width: 100%;
  height: 100%;
  max-width: 428px;
  max-height: 796px;
  color: var(--primary-white-color);
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  font-family: Inter;
  display: flex;
}

.contact-menu-nav {
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-size: 1.2rem;
  font-weight: 500;
  line-height: 1.5;
  list-style: none;
  display: block;
}

.model-menu-switch {
  width: 46px;
  height: 20px;
  fill: none;
  stroke: #000;
  stroke-width: 2px;
  position: fixed;
  bottom: 20px;
  left: 20px;
}

